.skills-containeer {
    display: flex;
    flex-direction: column;
}

.block-name {
    margin: 1rem;
}

.block-name h1 {
    color: #fff;
}

.block-list-skills {
    color: #fff;
    font-weight: 500;
    font-size: 35px;
    display: flex;
    flex-wrap: wrap;
}

.block-column {
    margin-top: 30px;
    padding-left: 40px;
    margin-left: 60px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
}

.skill {
    border: 1px solid gray;
    width: 300px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.skill:hover {
    background: linear-gradient(blue, 10%, pink);
}

.skill a {
    text-decoration: none;
    color: #fff;
}

@media screen and (max-width: 1536px) {
    .block-list-skills {
        font-size: 20px;
        margin-bottom: 50px;
        margin-top: -40px;
    }
    .skill {
        width: 290px;
        height: 90px;
    }
    .block-column {
        margin-left: 0;
    }
    .block-name h1 {
        font-size: 30px;
        margin-top: -15px;
    }
}
